*, :after, :before {
  box-sizing: border-box;
}

body, p, input, h1, ul, li, h4, h2, h5, h3 {
  margin: 0;
  padding: 0;
}

a, img, svg, button {
  cursor: pointer;
}

p, h1, h2, h3, h4, h5, div {
  -webkit-user-select: none;
  user-select: none;
  -khtml-user-select: none;
  -o-user-select: none;
}

h1, h2, h3, h4, h5, h6, p, span, a, li, td, th {
  color: #2b2925;
}

@media (prefers-color-scheme: dark) {
  h1, h2, h3, h4, h5, h6, p, span, a, li, td, th {
    color: #2b2925;
  }
}

.page {
  scroll-behavior: smooth;
  color: #2b2925;
  font-family: Cormorant Infant, Georgia, Times New Roman, serif;
  position: relative;
}

.page__body {
  background-image: url("background-mobile.d4186673.jpg");
  background-position: -390px -491px;
}

.page__body--with-menu {
  overflow: hidden;
}

.page__menu {
  transition: transform .4s;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu--opened {
  transform: translateX(0);
}

.container {
  margin: 0 auto;
}

@media (width >= 320px) and (width <= 767.5px) {
  .container {
    max-width: 390px;
    padding: 0 16px;
  }
}

@media (width >= 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 80px;
  }
}

@media (width >= 768px) {
  .grid {
    display: grid;
  }
}

@media (width >= 1280px) {
  .grid {
    grid-template-columns: repeat(12, 84.6667px);
