.burger-menu {
  cursor: pointer;
  z-index: 2;
  flex-direction: column;
  width: 40px;
  padding: 8px;
  display: flex;
  position: relative;
  transform: scaleX(-1);
}

.burger-menu__top-line, .burger-menu__middle-line, .burger-menu__bottom-line {
  box-sizing: border-box;
  border: .1px solid #000;
  margin-bottom: 5.93px;
  transition: all .4s cubic-bezier(.5, -.6, .32, .5);
}

.burger-menu__top-line:first-of-type {
  width: 50%;
}

.burger-menu__middle-line:nth-of-type(2) {
  width: 100%;
}

.burger-menu__bottom-line:nth-of-type(3) {
  width: 75%;
  margin-bottom: 0;
}

.burger-menu__opener {
  opacity: 0;
  width: 40px;
  height: 30px;
  position: absolute;
  top: 0;
  right: 0;
}

.burger-menu__opener[type="checkbox"]:checked ~ span:first-of-type {
  transform-origin: bottom;
  width: 13px;
  transform: rotateZ(-45deg)translate(5px, 9px);
}

.burger-menu__opener[type="checkbox"]:checked ~ span:nth-of-type(2) {
  transform-origin: top;
  width: 26px;
  transform: rotateZ(45deg);
}

.burger-menu__opener[type="checkbox"]:checked ~ span:nth-of-type(3) {
  transform-origin: bottom;
  width: 13px;
  transform: translate(2px, -3px)rotateZ(-45deg);
}

.header {
  background-image: url("main-photo-mobile.7da86cd7.jpg");
  background-position-x: 50%;
  height: 667px;
  position: relative;
}

.header__top-actions {
  background-color: #fff9;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.header__text {
  color: #987838;
  cursor: default;
  text-align: center;
  letter-spacing: .12em;
  background-image: linear-gradient(to right, #ebe9ea, #f9f9f9);
  height: 93px;
  font-family: Cormorant Infant, serif;
